.navigation {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.navigation__item {
  font-size: 1.4rem;
  margin: 0 2rem;
  padding: 1rem;
  text-decoration: none;
  color: #ffffff;
  border: 2px solid transparent;
}

.navigation__item:hover {
  color: #6cd265;
}

.navigation__item--active {
  border: 2px solid #ffffff;
}
